import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EnquirymaildataService } from 'src/app/enquirymaildata.service';

@Component({
  selector: 'app-mailenquiry',
  templateUrl: './mailenquiry.component.html',
  styleUrls: ['./mailenquiry.component.css']
})
export class MailenquiryComponent implements OnInit {
  record: any;
  constructor(private data: ActivatedRoute,
              private enquieryservice: EnquirymaildataService) {
        const id = this.data.snapshot.paramMap.get('id');
        console.log('id', id);
        this.getenquirymaildatabyID(id);
}

ngOnInit() {
}
      getenquirymaildatabyID(id) {
      this.enquieryservice.getenquirymaildatabyID(id).subscribe(res => {
      console.log('data by id ;' , res);
      this.record = res;
      });
}

}
