import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes} from '@angular/router';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { DashboardComponent } from './home/dashboard/dashboard.component';
import { MailboxComponent } from './home/mailbox/mailbox.component';
import { CareerComponent } from './home/mailbox/career/career.component';
import { EnquiryComponent } from './home/mailbox/enquiry/enquiry.component';
import { MailcareerComponent } from './home/mailbox/mailcareer/mailcareer.component';
import { MailenquiryComponent } from './home/mailbox/mailenquiry/mailenquiry.component';
import { LoginComponent } from './login/login.component';
import { HttpClientModule } from '@angular/common/http';
import { HireResourceComponent } from './home/mailbox/hire-resource/hire-resource.component';
import { MailhireResourceComponent } from './home/mailbox/mailhire-resource/mailhire-resource.component';
import { AuthGuard } from './auth.guard';
import { ReactiveFormsModule } from '@angular/forms';
import { HomeComponent } from './home/home.component';
import { ToastrModule } from 'ngx-toastr';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

const appRoutes: Routes = [
  {path: '', redirectTo: 'login', pathMatch: 'full'},
  {path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard]},
  {path: 'mailbox', component: MailboxComponent},
  {path: 'career', component: CareerComponent},
  {path: 'mailcareer', component: MailcareerComponent},
  {path: 'enquiry', component: EnquiryComponent},
  {path: 'mailenquiry', component: MailenquiryComponent},
  {path: 'login', component: LoginComponent},
];

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    MailboxComponent,
    CareerComponent,
    EnquiryComponent,
    MailcareerComponent,
    MailenquiryComponent,
    LoginComponent,
    HireResourceComponent,
    MailhireResourceComponent,
    HomeComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    ReactiveFormsModule,
    ToastrModule.forRoot(),
    BrowserAnimationsModule
  ],
  providers: [AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }

