import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HireResourcemailServiceService } from 'src/app/hire-resourcemail-service.service';

@Component({
  selector: 'app-hire-resource',
  templateUrl: './hire-resource.component.html',
  styleUrls: ['./hire-resource.component.css']
})
export class HireResourceComponent implements OnInit {

  public careermails = [];
  hireResourceMailRecords: any;
  constructor(private hireresourcemaildata: HireResourcemailServiceService) {   }

  ngOnInit() {
    this.getCommet();
  }


  getCommet() {
    this.hireresourcemaildata.getcomments().subscribe( res => {
      this.hireResourceMailRecords = res.records;
    });
  }

  refresh() {
    this.getCommet();
  }
}






