import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class HireResourcemailServiceService {

  getHireResourcemails: any;
  baseUrl: string = environment.baseUrl;
    constructor(private httpclient: HttpClient) {}
      getcomments(): Observable<any> {
        return this.httpclient.get(this.baseUrl + 'fetchAll_Hire_resource_mails.php');
       }
       getmaildatabyID(id): Observable<any> {
        return this.httpclient.get( this.baseUrl + 'fetch_hire_mails_by_id.php' + '?id=' + id);
       }

}






