import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EnquirymaildataService {
  baseUrl: string = environment.baseUrl;
  constructor(private httpclient: HttpClient) {}
    getEnquieryMails(): Observable<any> {
      return this.httpclient.get(this.baseUrl + 'fetchAllenquirymails.php');
     }
     getenquirymaildatabyID(Id) {
      return this.httpclient.get(this.baseUrl + 'fetch-enquiry-by-Id.php?' + 'id=' + Id);
     }
}


