import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CareermaildataService } from 'src/app/careermaildata.service';
import { HireResourcemailServiceService } from 'src/app/hire-resourcemail-service.service';

@Component({
  selector: 'app-mailhire-resource',
  templateUrl: './mailhire-resource.component.html',
  styleUrls: ['./mailhire-resource.component.css']
})
export class MailhireResourceComponent implements OnInit {
  hireresourceMailRecords: any;
  record: any;
  constructor(
    private hireresourcemaildata: HireResourcemailServiceService,
    private data: ActivatedRoute) {
      const id = this.data.snapshot.paramMap.get('id');
      console.log('id', id);
      this.getmaildatabyID(id);
    }

  ngOnInit() {
    this.getCommet();
  }
  getmaildatabyID(id: any) {
    this.hireresourcemaildata.getmaildatabyID(id).subscribe( res => {
      console.log('hireresourcemaildata : ' , res);
      console.log('hireresourcemaildata: ' , res);
      this.record = res;
    });
  }
  getCommet() {
    this.hireresourcemaildata.getcomments().subscribe( res => {
      console.log('res : ' , res);
      console.log('records: ' , res.records);
      this.hireresourcemaildata = res.records;
    });
  }


}


