import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CareermaildataService } from 'src/app/careermaildata.service';

@Component({
  selector: 'app-mailcareer',
  templateUrl: './mailcareer.component.html',
  styleUrls: ['./mailcareer.component.css']
})
export class MailcareerComponent implements OnInit {
  careerMailRecords: any;
  record: any;
  constructor(
    private careermaildata: CareermaildataService,
    private data: ActivatedRoute) {
      const id = this.data.snapshot.paramMap.get('id');
      console.log('id', id);
      this.getmaildatabyID(id);
    }

  ngOnInit() {
    this.getCommet();
  }

  getmaildatabyID(id: any) {
    this.careermaildata.getmaildatabyID(id).subscribe( res => {
      console.log('res : ' , res);
      console.log('records: ' , res);
      this.record = res;
    });
  }
  getCommet() {
    this.careermaildata.getcomments().subscribe( res => {
      console.log('res : ' , res);
      console.log('records: ' , res.records);
      this.careerMailRecords = res.records;
    });
  }
  download(filename ) {
      console.log(filename);
      // this.careermaildata.downloadFile(filename).subscribe( res => {
      //   console.log( 'result :', res);
      // });
  }
}
