import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './home/dashboard/dashboard.component';
import { LoginComponent } from './login/login.component';
import { CareerComponent } from './home/mailbox/career/career.component';
import { EnquiryComponent } from './home/mailbox/enquiry/enquiry.component';
import { MailcareerComponent } from './home/mailbox/mailcareer/mailcareer.component';
import { MailenquiryComponent } from './home/mailbox/mailenquiry/mailenquiry.component';
import { HireResourceComponent } from './home/mailbox/hire-resource/hire-resource.component';
import { MailhireResourceComponent } from './home/mailbox/mailhire-resource/mailhire-resource.component';
import { HomeComponent } from './home/home.component';
import { AuthGuard } from './auth.guard';

const routes: Routes = [
  { path : '', redirectTo : 'login' , pathMatch : 'full'},
  { path : 'login' , component : LoginComponent},
  { path : 'home', component: HomeComponent , canActivate: [AuthGuard],
 children : [
   {path : '', redirectTo: 'dashboard', pathMatch : 'full' },
   {path : 'dashboard' , component : DashboardComponent},
   { path: 'mailbox',
    children: [
      { path: '', redirectTo: 'career', pathMatch: 'full' },
      { path: 'career', component: CareerComponent },
      { path: 'enquiry', component: EnquiryComponent },
      { path: 'hire-resource', component: HireResourceComponent },
      { path: 'mailcareer/:id', component: MailcareerComponent },
      { path: 'mailenquiry/:id', component: MailenquiryComponent },
      { path: 'mailhire-resource/:id', component: MailhireResourceComponent },
   ]}
   ]},
  // { path : 'dashboard' , component : DashboardComponent},
  {path: '**', redirectTo: '' }
  ];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
