import { Component, OnInit } from '@angular/core';
import { EnquirymaildataService } from 'src/app/enquirymaildata.service';


@Component({
  selector: 'app-enquiry',
  templateUrl: './enquiry.component.html',
  styleUrls: ['./enquiry.component.css']
})
export class EnquiryComponent implements OnInit {
  enquieryMailData: any;
  constructor(private service: EnquirymaildataService) { }

  ngOnInit() {
    this.getEnquieryMails();
  }

  getEnquieryMails() {
    this.service.getEnquieryMails().subscribe(res => {
      this.enquieryMailData = res.records;
    });
  }

  refresh() {
    this.getEnquieryMails();
  }

}
